<template>
  <v-card>
    <v-data-table
      :headers="headers"
      :items="desserts"
      sort-by
      :single-expand="singleExpand"
      :expanded.sync="expanded"
      item-key="id"
      class="elevation-1"
      :search="search"
      :options.sync="options"
      :server-items-length="totalDesserts"
      @dblclick:row="dblclickrow"
    >

      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>合同列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-on="on"
                v-bind="attrs"
                class="ml-4"
                @click="dialog = true"
              >mdi-cloud-upload</v-icon>
            </template>
            <span>上传合同文件</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            @blur="initialize"
          ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon class="mr-2" v-on="on" v-bind="attrs" @click="dblclickrow(item.id)">mdi-pencil</v-icon>
          </template>
          <span>阅读</span>
        </v-tooltip>
      </template>

      <template v-slot:no-data>
        <small>暂无数据</small>
      </template>

    </v-data-table>

    <v-dialog v-model="dialog" max-width="800">
      <v-card>
        <v-card-title class="headline">上传文件</v-card-title>

        <v-card-text>
          <v-row align="center" justify="center">
              <v-col cols="6">
                <upload-file action="/upload" :imageStyle="imageStyle" text="请上传源文件"></upload-file>
              </v-col>

              <v-col cols="6">
                <upload-file action="/upload" :imageStyle="imageStyle" text="请上传对比文件"></upload-file>
              </v-col>
            </v-row>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">取消</v-btn>
          <v-btn color="green darken-1" text @click="dialog = false">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>


<script>
export default {
  data: () => ({
    expanded: [],
    singleExpand: true,
    dialog: false,
    search: "",
    // 分页信息
    options: {},
    totalDesserts: 0,
    headers: [
      {
        text: "公司名称",
        align: "start",
        sortable: false,
        value: "companyName"
      },
      { text: "客户数量", value: "customerNumber" },
      { text: "是否开启", value: "enable" },
      { text: "创建人", value: "creator" },
      { text: "创建时间", value: "createtime" },
      { text: "到期时间", value: "expirationTime" },
      { text: "操作", value: "actions", sortable: false }
    ],
    desserts: [],
  }),

  computed: {},

  watch: {
    options: {
      handler(val) {
        this.options = val;
        this.initialize();
      },
      deep: true
    }
  },

  created() {
    // this.initialize();
  },

  methods: {
    initialize() {
      let getData = {
        pageNum: this.options.page,
        pageSize: this.options.itemsPerPage,
        name: this.search,
        sortBy: this.options.sortBy[0],
        sortDesc: this.options.sortDesc[0]
      };
      this.$http
        .post("/user/api/v1/company/selectCompanyList", getData)
        .then(res => {
          this.totalDesserts = res.data.total;
          this.desserts = res.data.records;
        });
    },

    //双击事件demo
    dblclickrow(id) {
      this.$router.push({ path: "/contractDetails"});
    },

    // 上传文件时间
    test(fileInfo) {
      console.log(fileInfo)
    }
  },

  components: {
    UploadFile: () => import("../../upload/UploadFile")
  }
};
</script>